body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  font-size: calc(30px + 2vmin);
  color: #2F90BC;
  font-weight: 100;
}

.App-link:hover, .App-link:focus {
  color: #00AFFF;
}

.App-link strong {
  font-weight: 400;
}

h1 {
  margin: 0;
  padding: 0;
}

h2 {
  margin-top: 0;
  padding: 0;
  font-size: calc(10px + 2vmin);
  font-weight: 200;
}

.file {
  font-size: calc(6px + 1vmin);
  color: #FAA;
  display: block;
}

.components div {
  margin: 1vmin;
  float: left;
}

.React-logo {
  -webkit-animation: React-logo-spin infinite 20s linear;
          animation: React-logo-spin infinite 20s linear;
  height: 40vmin;
}

.Yarn-cat {
  -webkit-animation: Yarn-cat-move infinite 2s ease;
          animation: Yarn-cat-move infinite 2s ease;
  height: 40vmin;
  position: relative;
  margin-left: -20vmin;
  margin-top: 7.5vmin;
}

@-webkit-keyframes React-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes React-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes Yarn-cat-move {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  50% {
    -webkit-transform: translateX(20%);
            transform: translateX(20%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes Yarn-cat-move {
  0% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  50% {
    -webkit-transform: translateX(20%);
            transform: translateX(20%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

.Comp {
  border: 1px solid white;
  border-radius: 12px;
  padding: 3vmin;
  background: #164A61;
}

.Comp h3 {
  font-size: calc(24px + 1vmin);
  margin: 0;
